export default (size,query) => {
  const sizeArr = String(size).split('');
  console.log(sizeArr);
  const lastIndex = sizeArr.length - 1;
  let string = '';
  if(Number(sizeArr[lastIndex]) === 1) {
    string = '';
  }
  else if(sizeArr[lastIndex] > 1 && sizeArr[lastIndex] < 5) {
    string = '';
  }
  return `  "${query}"  ${size} ${string}`;
}


