import isValidEmail from 'is-valid-email';

export const validateAuth = (values) => {
  const errors = {};
  if(!values.email || values.email === '') {
    errors.email = ' email';
  }
  else if(!isValidEmail(values.email)) {
    errors.email = ' email ';
  }

  if(!values.password || values.password === '') {
    errors.password = ' ';
  }
  else if(values.password && values.password.length < 8) {
    errors.password = '      8 ';
  }

  if(!values.username || values.username === '') {
    errors.username = '    ';
  }
  return errors;
};

export const validateComment = ({text}) => {
  const errors = {};
  if(text === '') {
    errors.text = '    ';
  }
  return errors;
};

export const validateSearch = ({query}) => {
  const errors = {};
  if(query === '') {
    errors.query = '  ';
  }
  return errors;
};

export const validateResetPassword = ({password,confirmPassword}) => {
  const errors = {};
  if(!password || password === '') {
    errors.password = ' ';
  }
  else if(password && password.length < 8) {
    errors.password = '      8 ';
  }
  if(!confirmPassword || confirmPassword === '') {
    errors.confirmPassword = ' ';
  }
  else if(confirmPassword && confirmPassword.length < 8) {
    errors.confirmPassword = '      8 ';
  }
  return errors;
};

export const validateAlbum = ({name,description}) => {
  const errors = {};
  if(name === '') {
    errors.name = '    ';
  }
  if(description === '') {
    errors.description = '    ';
  }
  return errors;
};



